body {
  font-family: 'Roboto', 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: #121212;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  color: #ffffff;
}

/* Основной класс App */
.App {
  text-align: center;
  font-family: 'Roboto', 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background-color: #121212;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  max-width: 1200px;
  margin: 0 auto;
  color: #ffffff;
}

/* Стили для заголовка */
.app-header {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 20px;
}

.app-header h1 {
  margin: 0;
  font-size: 28px;
  color: #ffffff;
  display: flex;
  align-items: center;
  gap: 8px;
}

.version-text {
  font-size: 12px;
  color: #888;
}

/* Стили для content area */
.app-content {
  flex: 1;
}

/* Стили для тестовой панели */
.test-panel {
  background-color: rgba(255, 255, 255, 0.05);
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.ip-display {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  font-size: 16px;
}

.ip-label {
  color: #999;
  margin-right: 5px;
}

.ip-value {
  color: #ffffff;
  font-weight: bold;
}

.test-options {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
  justify-content: center;
}

.option-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 200px;
}

.option-group label {
  font-size: 14px;
  color: #999;
  margin-bottom: 8px;
}

.targets-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  width: 100%;
}

.target-item {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 8px 12px;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s;
  display: inline-flex;
  align-items: center;
}

.target-item:hover, .target-item.selected {
  background-color: rgba(76, 175, 80, 0.5);
}

/* Fix для отображения городов вместо стран */
.city-label {
  margin-right: 5px;
}

.test-button {
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 24px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.test-button:hover {
  background-color: #45a049;
  transform: translateY(-2px);
}

.test-button:disabled {
  background-color: #555;
  cursor: not-allowed;
  transform: none;
}

/* Стили для результатов */
.results-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 40px;
}

.results-container {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.result-item {
  margin-bottom: 15px;
  transition: all 0.3s ease;
}

.result-item:last-child {
  margin-bottom: 0;
}

.result-title {
  color: #999;
  font-size: 14px;
  margin-bottom: 8px;
}

.result-value {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}

.loading {
  color: #4caf50;
  position: relative;
}

.loading:after {
  content: '...';
  animation: loading-dots 1.5s infinite;
}

@keyframes loading-dots {
  0% { content: '.'; }
  33% { content: '..'; }
  66% { content: '...'; }
}

.latency-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.latency-item {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: normal;
}

.server-name {
  color: #999;
}

.latency-value {
  color: #fff;
  font-weight: 600;
}

/* Footer styles */
.app-footer {
  margin-top: auto;
  padding: 20px 0;
  color: #666;
  font-size: 14px;
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

/* Language selector */
.language-selector {
  display: flex;
  gap: 10px;
}

.language-selector span {
  cursor: pointer;
  color: #999;
  padding: 5px 8px;
  border-radius: 4px;
  transition: all 0.2s;
  font-size: 14px;
}

.language-selector span:hover {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.1);
}

.language-selector .lang-active {
  background-color: rgba(76, 175, 80, 0.5);
  color: white;
}

/* Toggle switch для advanced options */
.advanced-options {
  margin-top: 10px;
}

.toggle-switch {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 10px;
  font-size: 14px;
  color: #999;
}

.toggle-switch input {
  height: 0;
  width: 0;
  visibility: hidden;
  position: absolute;
}

.switch-slider {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  margin-right: 10px;
  transition: background-color 0.3s;
}

.switch-slider:before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.3s;
}

input:checked + .switch-slider {
  background-color: #4caf50;
}

input:checked + .switch-slider:before {
  transform: translateX(16px);
}

/* Стили для истории тестов */
.test-history-container {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 30px;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 10px;
}

.history-header h2 {
  margin: 0;
  font-size: 20px;
  color: #fff;
}

.history-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.export-btn, .clear-history-btn {
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 20px;
  padding: 8px 12px;
  color: #fff;
  font-size: 12px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.export-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.clear-history-btn {
  background-color: rgba(244, 67, 54, 0.3);
}

.clear-history-btn:hover {
  background-color: rgba(244, 67, 54, 0.5);
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 400px;
  overflow-y: auto;
  padding: 5px;
}

.history-item {
  background-color: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  transition: background-color 0.3s;
  position: relative;
}

.history-item:hover {
  background-color: rgba(255, 255, 255, 0.12);
}

.history-item.selected {
  background-color: rgba(76, 175, 80, 0.2);
  box-shadow: 0 0 0 1px rgba(76, 175, 80, 0.5);
}

.history-item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.history-item-date {
  font-size: 14px;
  color: #999;
}

.network-quality-indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.history-item-summary {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-size: 14px;
}

.history-item-details {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.history-item-details h4 {
  margin: 0 0 10px 0;
  font-size: 16px;
  color: #fff;
}

.detail-table {
  max-height: 200px;
  overflow-y: auto;
  margin-bottom: 15px;
}

.detail-table table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.detail-table th, .detail-table td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.detail-table th {
  color: #999;
  font-weight: normal;
}

.advanced-metrics-details {
  margin-top: 10px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  font-size: 14px;
}

.toggle-details-btn {
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 8px 16px;
  color: #999;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.3s;
  margin-top: 10px;
}

.toggle-details-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.no-history-message {
  text-align: center;
  padding: 20px;
  color: #999;
  font-size: 14px;
  font-style: italic;
}

/* Стили для графиков тенденций */
.history-trends {
  margin-top: 30px;
}

.history-trends h3 {
  font-size: 18px;
  color: #fff;
  margin-bottom: 15px;
}

.chart-container {
  height: 300px;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 30px;
  position: relative;
}

/* Стили для индикаторов качества */
.quality-indicator {
  display: inline-flex;
  align-items: center;
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: bold;
  margin-left: 8px;
}

.quality-indicator.better {
  background-color: rgba(76, 175, 80, 0.2);
  color: #4caf50;
}

.quality-indicator.worse {
  background-color: rgba(244, 67, 54, 0.2);
  color: #f44336;
}

.quality-indicator.neutral {
  background-color: rgba(255, 193, 7, 0.2);
  color: #ffc107;
}

/* Стили для всплывающих подсказок на карте */
.map-popup {
  color: #ffffff;
}

.map-popup h3 {
  margin: 0 0 8px 0;
  color: #ffffff;
}

.map-popup p {
  margin: 5px 0;
  font-size: 14px;
  color: #cccccc;
}

.map-popup .latency-value {
  font-weight: bold;
  margin-left: 5px;
}

.map-popup .quality-indicator {
  margin-top: 8px;
  display: inline-block;
}

/* Адаптивный дизайн */
@media (max-width: 768px) {
  .App {
    padding: 10px;
  }
  
  .App header h1 {
    font-size: 1.5rem;
  }
  
  .app-header {
    flex-direction: column;
    gap: 15px;
    padding: 15px 0;
  }
  
  .test-options {
    flex-direction: column;
    align-items: center;
  }
  
  .option-group {
    width: 100%;
    align-items: center;
  }
  
  .targets-list {
    justify-content: center;
  }
  
  .history-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .history-actions {
    width: 100%;
    justify-content: space-between;
  }
}

@media (max-width: 480px) {
  .App {
    padding: 5px;
  }
  
  .App header {
    flex-direction: column;
    gap: 10px;
  }
  
  .test-options .option-group {
    flex-direction: column;
  }
}

/* Анимация для переливающегося текста заголовка */
@keyframes rainbow-text {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.rainbow-title {
  background: linear-gradient(90deg, #ff0000, #ff9900, #33cc33, #00c6ff, #0072ff, #cc00ff, #ff0066);
  background-size: 1400% 100%;
  animation: rainbow-text 8s ease infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  font-size: 48px;
  letter-spacing: -2px;
  margin: 48px 0 16px 0;
  font-family: "Inter", "Segoe UI", "Arial", sans-serif;
  text-align: center;
}

/* Стили для интерактивной карты мира */
.world-map-container {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  padding: 20px;
  margin: 20px 0;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.world-map-container h3 {
  color: #ffffff;
  text-align: center;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.4rem;
}

.world-map {
  position: relative;
  width: 100%;
  height: 400px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.map-legend {
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
}

.legend-item {
  margin: 0 10px;
  font-size: 12px;
  display: flex;
  align-items: center;
}

.color-indicator {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 5px;
  border-radius: 50%;
}

.color-indicator.good {
  background-color: #00ff00;
}

.color-indicator.bad {
  background-color: #ff0000;
}

/* Fix for Leaflet map controls */
.map-controls {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1000;
  background: white;
  border-radius: 4px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.4);
  padding: 5px;
  display: flex;
  flex-direction: column;
}

.map-controls button {
  margin: 2px 0;
  border: none;
  background: white;
  width: 30px;
  height: 30px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.map-controls button:hover {
  background: #f0f0f0;
}

/* Fix for Leaflet popup styling */
.leaflet-popup-content-wrapper {
  background-color: rgba(50, 50, 50, 0.95);
  color: white;
  border-radius: 8px;
}

.leaflet-popup-content {
  margin: 10px 12px;
}

.leaflet-popup-tip {
  background-color: rgba(50, 50, 50, 0.95);
}

.map-popup h3 {
  margin: 0 0 8px 0;
  color: #333;
}

.map-popup p {
  margin: 0;
  font-size: 14px;
}

/* Fix for view mode toggle buttons */
.view-mode-toggle {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 15px;
}

.view-mode-toggle button {
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 20px;
  padding: 8px 16px;
  color: #999;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s;
}

.view-mode-toggle button:hover {
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
}

.view-mode-toggle button.active {
  background-color: rgba(76, 175, 80, 0.5);
  color: white;
}

/* Стили для карты ошибки */
.map-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 300px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  padding: 20px;
  text-align: center;
  margin: 10px 0;
}

.map-error button {
  margin-top: 15px;
  cursor: pointer;
}

/* Адаптивный дизайн для карты на мобильных устройствах */
@media (max-width: 768px) {
  .world-map {
    height: 300px;
  }
  
  .map-legend {
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
  
  .world-map-container {
    padding: 15px;
  }
}

/* Fix for App class in media queries */
@media (max-width: 767px) {
  .App {
    padding: 10px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .App {
    padding: 15px;
  }
}

@media (min-width: 992px) {
  .App {
    padding: 20px;
  }
}
